import Vue from 'vue'
import Router from 'vue-router'

import Index from '@/components/Index'
import Project from '@/components/Project'
import Object from '@/components/Object'
import Auth from '@/components/Auth'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/project',
      name: 'Project',
      component: Project
    },
    {
      path: '/object',
      name: 'Object',
      component: Object
    },
    {
      path: '/auth',
      name: 'Auth',
      component: Auth
    }
  ]
})
